<template>
    <div class="video-index" id="video-index">
        <div class="com-title-bar">
            <div class="box van-hairline--bottom">
                <div class="back" @click="$routerBack"></div>
                <div class="title"></div>
                <div class="action">
                </div>
            </div>
        </div>

        <div class="player" id="dplayer"></div>
        <div class="residue" @click="$router.push('/user/popularize')">
            <div class="text">
                <div>{{ video.viewTimes.remaining }}</div>
                <div></div>
            </div>
            <div class="ico">
                <img src="../../assets/video/residue_ico.png">
            </div>
        </div>
        <div class="data">
            <div class="title">
                <div class="text">{{ video.translate_title }}</div>
                <div class="more"></div>
            </div>
            <div class="introduce">
                {{ video.introduce }}
            </div>
            <div class="info">
                <div class="item star">
                    <div class="ico">
                        <van-icon name="star" color="#FFEA03"/>
                    </div>
                    <div class="text">{{ video.star }}</div>
                </div>
                <div class="item date">
                    {{ video.create_time }}
                </div>
                <div class="item category">
                    {{ video.category.title }}
                </div>
            </div>
            <div class="tags">
                <div class="item" v-if="index < 5" v-for="(item,index) in video.tags" :key="index">
                    {{ item }}
                </div>
            </div>
            <div class="action">
                <div class="item" :class="this.video.thumb === true?'zan_active':'zan'" @click="zanAction(true)">
                    <div class="text"></div>
                </div>
                <div class="item" :class="this.video.thumb === false?'unzan_active':'unzan'" @click="zanAction(false)">
                    <div class="text"></div>
                </div>
                <div class="item" :class="this.video.favorite === true?'favorite_active':'favorite'" @click="favoriteAction()">
                    <div class="text"></div>
                </div>
                <div class="item share">
                    <div class="text"></div>
                </div>
            </div>
        </div>
        <div class="tabs" :style="tabs.style">
            <van-tabs :v-model="tabs.active" :line-height="2" color="#FFEA03" animated swipeable sticky>
                <van-tab title="" name="prefers">
                    <div class="prefers">
                        <van-list v-model="prefers.loading" :finished="prefers.finished" finished-text="" @load="initPrefers">
                            <div class="item" v-for="(item,index) in prefers.data" :key="index" @click="videoHandel(item.uuid)">
                                <div class="image">
                                    <img :src="item.thumbnail">
                                </div>
                                <div class="info">
                                    <div class="title">
                                        {{ item.translate_title }}
                                    </div>
                                    <div class="tags">
                                        <div class="tag" v-if="key < 3 && value!==''" v-for="(value,key) in item.tags" :key="key">{{ value }}</div>
                                    </div>
                                    <div class="star">
                                        <div class="ico">
                                            <van-icon name="star" color="#FFEA03"/>
                                        </div>
                                        <div class="text">{{ item.star }}</div>
                                    </div>
                                </div>
                            </div>
                        </van-list>
                    </div>
                </van-tab>
                <van-tab title="" name="comment">
                    <div class="comment">
                        <div class="empty"></div>
                    </div>
                </van-tab>
            </van-tabs>
        </div>
    </div>
</template>

<script>
    import {Icon, Skeleton, Tab, Tabs, List} from 'vant';
    import 'dplayer/dist/DPlayer.min.css';
    import DPlayer from 'dplayer';
    import Hls from 'hls.js';

    export default {
        components: {
            [Icon.name]: Icon,
            [Skeleton.name]: Skeleton,
            [Tab.name]: Tab,
            [Tabs.name]: Tabs,
            [List.name]: List
        },
        data() {
            return {
                dplayer: null,
                tabs: {
                    active: 'prefers',
                    style: {
                        height: '100px'
                    }
                },
                view: {
                    display: false
                },
                video: {
                    title: '',
                    translate_title: '',
                    star: 0,
                    create_time: '0000-00-00',
                    category: {},
                    viewTimes: {
                        remaining: null
                    },
                    thumb: null,
                    favorite: null
                },
                action: {},
                prefers: {
                    data: [],
                    loading: false,
                    finished: false,
                    page: 1
                },
                touch: {
                    startX: 0,
                    startY: 0
                }
            }
        },
        watch: {
            '$route'(to, from) {
                this.dplayer.destroy();
                this.video = {
                    title: '',
                    star: 0,
                    create_time: '0000-00-00',
                    category: {},
                    residue: 0,
                    thumb: null,
                    favorite: null
                },
                    this.initVideo();
                this.prefers = {
                    data: [],
                    loading: true,
                    finished: false,
                    page: 1
                };
                this.initPrefers();
            }
        },
        mounted() {
            this.initTouch();
            this.initVideo();
            setTimeout(() => {
                this.tabs.style.height = window.innerHeight + 100 + 'px';
            }, 500)
        },
        destroyed() {
            if (this.dplayer) {
                this.dplayer.destroy();
            }
        },
        methods: {
            initTouch() {
                document.getElementById('video-index').addEventListener('touchstart', (e) => {
                    this.touch.startX = e.touches[0].pageX;
                    this.touch.startY = e.touches[0].pageY;
                });
                document.getElementById('video-index').addEventListener('touchmove', (e) => {
                    let moveEndX = e.changedTouches[0].pageX;
                    let moveEndY = e.changedTouches[0].pageY;
                    let X = moveEndX - this.touch.startX;
                    let Y = moveEndY - this.touch.startY;
                    if (Math.abs(Y) > Math.abs(X) && Y < 0) {
                        return;
                    }
                    if (Math.abs(Y) > Math.abs(X) && Y > 0) {
                        return;
                    }
                    e.preventDefault();
                });
            },
            initVideo() {
                this.$axios.get('/api/app/video/store', {
                    uuid: this.$route.query.uuid
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.video = result;

                            if (result['viewTimes']['remaining'] === 0) {

                                this.$dialog.confirm({
                                    title: '',
                                    message: '',
                                    confirmButtonText: '',
                                    confirmButtonColor: '#F9DC36'
                                }).then(() => {

                                    this.$router.push('/user/popularize');

                                }).catch(() => {
                                    if (window.plus) {
                                        plus.webview.currentWebview().back();
                                    } else {
                                        this.$router.go(-1);
                                    }
                                });

                                return;
                            }


                            let m3u8 = result.m3u8;
                            let quality = [];

                            if (m3u8['720']) {
                                quality.push({
                                    name: '',
                                    url: m3u8['720'],
                                    type: 'customHls',
                                })
                            }

                            if (m3u8['480']) {
                                quality.push({
                                    name: '',
                                    url: m3u8['480'],
                                    type: 'customHls',
                                })
                            }

                            if (m3u8['360']) {
                                quality.push({
                                    name: '',
                                    url: m3u8['360'],
                                    type: 'customHls',
                                })
                            }

                            this.dplayer = new DPlayer({
                                container: document.getElementById('dplayer'),
                                video: {
                                    quality: quality,
                                    defaultQuality: (quality.length - 1),
                                    pic: result.thumbnail,
                                    type: 'customHls',
                                    autoplay: true,
                                    customType: {
                                        'customHls': function (video, player) {
                                            let config = {
                                                maxBufferLength: 300,
                                                xhrSetup: function (xhr, url) {
                                                    //console.log(url);
                                                    //xhr.withCredentials = true; // do send cookies
                                                }
                                            };
                                            const hls = new Hls(config);
                                            hls.loadSource(video.src);
                                            hls.attachMedia(video);
                                        }
                                    }
                                }
                            });
                            this.dplayer.on('canplay', () => {
                                setTimeout(() => {
                                    this.dplayer.play();
                                }, 100);
                            });
                        }
                    });
            },
            initPrefers() {
                this.$axios.get('/api/app/video/prefers', {
                    uuid: this.$route.query.uuid,
                    page: this.prefers.page
                })
                    .then(response => {
                        let code = response.data.code;
                        let result = response.data.result;
                        if (code === 200) {
                            this.prefers.loading = false;

                            if (result.data.length === 0) {
                                this.prefers.finished = true;
                                return;
                            }

                            this.prefers.page++;
                            this.prefers.data = this.prefers.data.concat(result.data);
                        }
                    });
            },
            videoHandel(uuid) {
                window.scrollTo(0, 0);
                this.$router.replace('/video/index?uuid=' + uuid)
            },
            zanAction(status) {
                if (this.video.thumb !== null) {
                    return;
                }
                this.$axios.post('/api/app/video/thumb/action', {
                    uuid: this.$route.query.uuid,
                    status: status
                })
                    .then(response => {
                        let code = response.data.code;
                        if (code === 200) {
                            this.video.thumb = status;
                        }
                    })
            },
            favoriteAction() {
                this.$axios.post('/api/app/video/favorite/action', {
                    uuid: this.$route.query.uuid,
                    status: !this.video.favorite
                })
                    .then(response => {
                        let code = response.data.code;
                        if (code === 200) {
                            this.video.favorite = !this.video.favorite;
                        }
                    })
            }
        }
    }
</script>

